import { Container, Table, Image } from 'react-bootstrap';
import Curriculum from './../../JSON/Curriculum.json';
import ailogo from './ailogo.png';

const AboutAI = () =>
{
    const curric = Curriculum.map(sub => 
        {
            if (sub.class === undefined)
            {
                return (
                    <tr key={sub.subject}>
                        <td>{sub.subject}</td>
                    </tr>
                );
            }
            else
            {
                return (
                    <tr className='table-secondary' key={sub.class}>
                        <th>{sub.class}</th>
                    </tr>
                );
            }
        });

    return (
        <>
            <Container className='text-center fluid'>
                <Image src={ailogo} fluid/>
                <h1>ห้องเรียนพิเศษวิทยาศาสตร์ - คณิตศาสตร์และปัญญาประดิษฐ์ (AI)</h1><br/><br/>
                <h2>รายวิชาที่เพิ่มเติมในหลักสูตร</h2>
                <Table responsive bordered>
                    <thead className='align-middle'>
                        {curric}
                    </thead>
                </Table>
            </Container>
        </>
    );
}

export default AboutAI;