import { useState, useEffect } from 'react';
import { Container, Form, Button, Row, Col, Table, Spinner } from 'react-bootstrap';
import axios from 'axios';
import moment from 'moment';
import classnames from 'classnames';

const AdminHomework = () => 
{
    const [homework, setHomework] = useState({});
    const [dbhomework, setHW] = useState([]);
    const [deleting, setDeleting] = useState(false);
    const [loading, setLoading] = useState(true);

    const handleHWChange = (event) => 
    {
		const name = event.target.name;
		const value = event.target.value;
		setHomework((values) => ({ ...values, [name]: value }));
		const token = localStorage.getItem("accessToken");
		setHomework((values) => ({ ...values, accessToken: token }));
	}

    const handleHWSubmit = (event) => 
    {
		event.preventDefault();
		axios.post(`${process.env.REACT_APP_API_LINK}/homework/add`, homework)
			.then((response) => 
            {
				alert(response.data.message);
			});
	}

    const handleHWQuery = () =>
    {
        axios.get(`${process.env.REACT_APP_API_LINK}/homework/get`)
            .then((response) => 
            {
                setHW(
                    response.data.sort((a, b) =>
                    {
                        return moment(b.dateDue).unix() - moment(a.dateDue).unix();
                    })
                )
            })
            .then(() => 
            {
                setLoading(false);
            })
    }

    const handleDelete = (event) =>
    {
        setDeleting(false);
        axios.delete(`${process.env.REACT_APP_API_LINK}/homework/get/${event}`).then((response) =>
        {
            setDeleting(false);
            handleHWQuery();
        })
    }

    useEffect(() => 
    {
        setLoading(true);
        handleHWQuery();
    }, [])

    const homeworkMap = dbhomework.map((hw) => {
		let homeworkColor = classnames({
			"table-danger": moment(hw.DateDue).unix() <= moment().unix(),
			"table-warning":
				moment(hw.DateDue).unix() > moment().unix() &&
				moment(hw.DateDue).unix() <= moment().add(1, "days").unix(),
			"table-success":
				moment(hw.DateDue).unix() > moment().add(1, "days").unix() &&
				moment(hw.DateDue).unix() <= moment().add(7, "days").unix(),
			"table-light": moment(hw.DateDue).unix() > moment().add(7, "days").unix(),
		});

		return (
			<tr key={hw._id} className={homeworkColor}>
				<td>{hw.Subject}</td>
				<td>{hw.Topic}</td>
				<td>{hw.Description}</td>
				<td>{moment(hw.DateDue).format('DD/MM/YYYY, HH:mm')}</td>
                <td>{deleting ? <Button variant='primary btn-block'>กำลังลบ</Button>: <Button variant='danger btn-block' type='submit' onClick={() => handleDelete(hw._id)}>ลบ</Button>}</td>
			</tr>
		);
	});

    return (
        <>
            <Container className='mt-5 p-5 m-auto shadow-sm rounded header' style={{maxWidth: '60%'}}>
                <h1 className='text-center text-light'>ระบบเพิ่มการบ้าน</h1>
                <Form onSubmit={handleHWSubmit}>
                    <Row>
                        <Col>
                            <Form.Label className='text-light'>รายวิชา</Form.Label>
                            <Form.Select aria-label='Default select example' name='subject' onChange={handleHWChange}>
                                <option selected disable hidden>กดเพื่อเลือกวิชา</option>
                                <option value='วิทยาการคำนวณ 2'>วิทยาการคำนวณ 2</option>
                                <option value='อินเทอร์เน็ตเพื่อสรรพสิ่ง'>อินเทอร์เน็ตเพื่อสรรพสิ่ง</option>
                                <option value='โครงสร้างฐานข้อมูลและฐานข้อมูลสำหรับปัญญาประดิษฐ์ 1'>โครงสร้างฐานข้อมูลและฐานข้อมูลสำหรับปัญญาประดิษฐ์ 1</option>
                                <option value='ฟิสิกส์สำหรับปัญญาประดิษฐ์ 3'>ฟิสิกส์สำหรับปัญญาประดิษฐ์ 3</option>
                                <option value='เคมี 3'>เคมี 3</option>
                                <option value='ชีววิทยา 2'>ชีววิทยา 2</option>
                                <option value='คณิตศาสตร์พื้นฐาน 3'>คณิตศาสตร์พื้นฐาน 3</option>
                                <option value='คณิตศาสตร์เพิ่มเติมสำหรับปัญญาประดิษฐ์ 3'>คณิตศาสตร์เพิ่มเติมสำหรับปัญญาประดิษฐ์</option>
                                <option value='ภาษาอังกฤษสำหรับปัญญาประดิษฐ์ 3'>ภาษาอังกฤษสำหรับปัญญาประดิษฐ์ 3</option>
                                <option value='ภาษาอังกฤษ 3'>ภาษาอังกฤษ 3</option>
                                <option value='ภาษาไทย 3'>ภาษาไทย 3</option>
                                <option value='การศึกษาค้นคว้าและสร้างองค์ความรู้'>การศึกษาค้นคว้าและสร้างองค์ความรู้</option>
                                <option value='ภูมิศาสตร์'>ภูมิศาสตร์</option>
                                <option value='กิจกรรมแนะแนว'>กิจกรรมแนะแนว</option>
                                <option value='การงานอาชีพ'>การงานอาชีพ</option>
                                <option value='สุขศึกษาและพลศึกษา 3'>สุขศึกษาและพลศึกษา 3</option>
                                <option value='ดนตรีไทยพื้นฐาน'>ดนตรีไทยพื้นฐาน</option>
                                <option value='กิจกรรมภายในโรงเรียน'>กิจกรรมภายในโรงเรียน</option>
                            </Form.Select>
                        </Col>
                    </Row><br/>
                    <Row>
                        <Col>
                            <Form.Group controlId='formBasicPassword'>
                                <Form.Label className='text-light'>หัวข้องาน</Form.Label>
                                <Form.Control type='text' placeholder='หัวข้องาน' name='topic' onChange={handleHWChange}/>
                            </Form.Group>
                        </Col>
                        <Col>
                            <Form.Group className='mb-3' controlId='formDateDue'>
                            <Form.Label className='text-light'>วันกำหนดส่ง</Form.Label>
                            <Form.Control type='datetime-local' name='dateDue' onChange={handleHWChange}/>
                            </Form.Group>
                        </Col>
                    </Row>
                    <Form.Group className='mb-3' controlId='formDescription'>
                        <Form.Label className='text-light'>คำอธิบายงาน</Form.Label>
                        <Form.Control as='textarea' rows={3} name='description' placeholder='คำอธิบายงาน' onChange={handleHWChange}/>
                    </Form.Group>
                    <Button variant='primary btn-block' type='submit'>ส่ง</Button>
                </Form>
            </Container><br/>
            <Container className='mt-5 p-5 m-auto shadow-sm rounded header' style={{maxWidth: '60%'}}>
                <h1 className='text-center text-light'>ระบบลบการบ้าน</h1><br/>
                <Table responsive striped bordered>
                    <thead>
                        <tr className='table-secondary'>
                            <th>รายวิชา</th>
                            <th>หัวข้องาน</th>
                            <th>คำอธิบายงาน</th>
                            <th>วันกำหนดส่ง</th>
                            <th>ปุ่มลบ</th>
                        </tr>
                        {homeworkMap}
                    </thead>
                </Table>
                {loading ? <Spinner animation="border" variant="primary" /> : <></>}
            </Container><br/>
        </>
    );
}

export default AdminHomework;