import { Container, Row, Col, Card } from 'react-bootstrap';
import Student from './../../JSON/Student.json';

const Member = () => 
{
    const teacherCard = Student.map(tch => 
    {
        if (tch.number > 37)
        {
            return (
                <Card key={tch.number}>
                    <Card.Img variant='top' src={tch.img}/>
                    <Card.Body>
                        <Card.Title>{tch.name} ({tch.nick})</Card.Title>
                        <Card.Text>{tch.pos}</Card.Text>
                    </Card.Body>
                </Card>
            );
        }
    });
    const studentCard = Student.map(std => 
    {
        if (std.number <= 36)
        {
            return (
                <>
                    <Col key={std.number} md>
                        <Card>
                            <Card.Img variant='top' src={std.img} fluid/>
                            <Card.Body>
                                <Card.Title>{std.name} ({std.nick})</Card.Title>
                                <Card.Text>{std.pos}</Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                </>
            );
        }
    });

    return (
        <>
            <Container className='text-center'>
                <h1>สมาชิกในห้องเรียน ม.5/15</h1><br/><br/>
                <h2>ครูที่ปรึกษาห้อง ม.5/15</h2>
                <Row sm={1} lg={3}>
                    {teacherCard}
                </Row><br/><br/>
                <h2>นักเรียนห้อง ม.5/15</h2>
                <Row sm={1} md={2} lg={3}>
                    {studentCard}
                </Row>
            </Container>
        </>
    );
}

export default Member;