import { Container, Navbar, Nav } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import './AdminHeader.css';

const AdminHeader = () =>
{
    const handleLogout = () => 
    {
        localStorage.removeItem("accessToken");
		window.location.reload(false);
		alert("Logout success");
    }

    return (
        <>
            <Navbar expand='xl' variant='dark' sticky='top' className='header'>
                <Container>
                    <LinkContainer to='/'>
                        <Navbar.Brand className='logo'>515 AI Official</Navbar.Brand>
                    </LinkContainer>
                    <Navbar.Toggle aria-controls="basic-navbar-nav"/>
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className='me-auto'>
                            <LinkContainer to='/'>
                                <Nav.Link className='link'>หน้าหลัก</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to='/member'>
                                <Nav.Link className='link'>สมาชิก</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to='/student'>
                                <Nav.Link className='link'>ใบรายชื่อ</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to='/homework'>
                                <Nav.Link className='link'>การบ้าน</Nav.Link>
                            </LinkContainer>
                            <LinkContainer to='/admin/addhomework'>
                                <Nav.Link className='link'>เพิ่มการบ้าน</Nav.Link>
                            </LinkContainer>
                            <Nav.Link className='link' href='https://drive.google.com/drive/folders/1L0J4UC6GTSBQ7YZxFYEINAdl5O3o13WO?usp=sharing'>ไฟล์งาน</Nav.Link>
                            <LinkContainer to='/aboutus'>
                                <Nav.Link className='link'>ห้องเรียนพิเศษ AI</Nav.Link>
                            </LinkContainer>
                            <LinkContainer onClick={handleLogout} to='/'>
                                <Nav.Link className='link'>Logout</Nav.Link>
                            </LinkContainer>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
            <div className='empty'/>
        </>
    );
}

export default AdminHeader;