import { Routes, Route } from 'react-router-dom';
import { isExpired, decodeToken } from "react-jwt";
import AdminHeader from './Components/AdminHeader';
import Header from './Components/Header';
import Home from './Pages/Home';
import Member from './Pages/Member';
import Student from './Pages/Student';
import Homework from './Pages/Homework';
import AddHomework from './Pages/AddHomework';
import AdminHomework from './Pages/AdminHomework';
import AboutAI from './Pages/AboutAI';
import Login from './Pages/Login';
import AddUser from './Pages/AddUser';
import NotFound from './Pages/NotFound';
import Construction from './Pages/Construction';

const App = () =>
{
  const token = localStorage.getItem('accessToken');
  const user = localStorage.getItem('user');
  const isTokenExpired = isExpired(token);
  const tokenOwner = decodeToken(token)?.username;

  const isAdmin = token && !isTokenExpired && tokenOwner === 'kinpkt'
  const isUser = token && !isTokenExpired && tokenOwner === user

  return (
    <>
      {isUser ? <AdminHeader/> : <Header/>}
      <Routes>
        <Route exact path='/' element={<Home/>}/>
        <Route path='/member' element={<Member/>}/>
        <Route path='/student' element={<Student/>}/>
        <Route path='/homework' element={<Homework/>}/>
        <Route path='/admin/addhomework' element={isAdmin ? <AdminHomework/> : isUser ? <AddHomework/> : <NotFound/>}/>
        <Route path='/aboutus' element={<AboutAI/>}/>
        <Route path='/login' element={<Login/>}/>
        <Route path='/admin/adduser' element={isAdmin ? <AddUser/> : <NotFound/>}/>
        <Route path='*' element={<NotFound/>}/>
      </Routes>
    </>
  );
}

export default App;