import { useState } from 'react';
import { Container, Form, Row, Col, Button } from 'react-bootstrap';
import axios from 'axios';
import aesjs from 'aes-js';

const AddUser = () => 
{
  const [user, setUser] = useState({})

  const handleUserChange = (event) => 
  {
		const name = event.target.name;
		const value = event.target.value;
		setUser((values) => ({ ...values, [name]: value }));
	}

  const handleUserSubmit = (event) => 
  {
    event.preventDefault();
    if (user.password !== user.confirmpassword) return alert('Password not matching.');
    const key = process.env.REACT_APP_AES_KEY.split(',').map((item) => {return parseInt(item, 10)})
    const passwordBytes = aesjs.utils.utf8.toBytes(user.password);
    const aesCtrl = new aesjs.ModeOfOperation.ctr(key, new aesjs.Counter(5));
    const encryptedBytes = aesCtrl.encrypt(passwordBytes);
    const encryptedHex = aesjs.utils.hex.fromBytes(encryptedBytes);
    axios.post(`${process.env.REACT_APP_API_LINK}/user/register`, {user: user.user, password: encryptedHex})
    .then((response) => 
    {
      alert(response.data.user);
    });
  }

  return (
      <>
        <Container className='mt-5 p-5 m-auto shadow-sm rounded header' style={{maxWidth: '60%'}}>
          <h1 className='text-center text-light'>ระบบเพิ่มผู้ใช้</h1>
          <Form onSubmit={handleUserSubmit}>
              <Row>
                  <Col>
                      <Form.Group controlId='formUsername'>
                          <Form.Label className='text-light'>ชื่อผู้ใช้ใหม่</Form.Label>
                          <Form.Control type='text' placeholder='กรอกชื่อผู้ใช้ใหม่' name='user' onChange={handleUserChange}/>
                      </Form.Group>
                  </Col>
              </Row><br/>
              <Row>
                  <Col>
                      <Form.Group controlId='formPassword'>
                          <Form.Label className='text-light'>รหัสผ่านผู้ใช้</Form.Label>
                          <Form.Control type='password' placeholder='กรอกรหัสผ่านผู้ใช้ใหม่' name='password' onChange={handleUserChange}/>
                      </Form.Group>
                  </Col>
                  <Col>
                      <Form.Group controlId='formConfirmPassword'>
                          <Form.Label className='text-light'>ยืนยันรหัสผ่าน</Form.Label>
                          <Form.Control type='password' placeholder='กรอกรหัสผ่านผู้ใช้อีกรอบ' name='confirmpassword' onChange={handleUserChange}/>
                      </Form.Group>
                  </Col>
              </Row><br/>
              <Button variant='primary btn-block' type='submit'>ส่ง</Button>
          </Form>
        </Container>
      </>
  )
}

export default AddUser;