import { useState, useEffect } from 'react';
import { Container, Table, Spinner } from 'react-bootstrap';
import Duty from './../../JSON/Duty.json';
import SatSchedule from './../../JSON/SatSchedule.json';
import TestSch from './../../JSON/TestSch.json';
import axios from 'axios';

const Home = () =>
{
    const [Schedule, setSchedule] = useState([]);
    const [loading, setLoading] = useState(true);

    const handleQuery = () => 
    {
        axios.get(`https://aigen2back.vercel.app/schedule/get`)
        .then((response) => {setSchedule(response.data)})
        .then(() => 
        {
            setLoading(false);
        })
    }

    useEffect(() => 
    {
        setLoading(true);
        handleQuery();
    }, [])

    const scheduleMap = Schedule.map((day) =>
    {
        const P1isLab = day.P1 === day.P2;
        const P2isLab = day.P2 === day.P3;
        const P3isLab = day.P3 === day.P4;
        const P6isLab = day.P6 === day.P7;
        const P7isLab = day.P7 === day.P8;
        const P8isLab = day.P8 === day.P9;

        return (
            <tr key={day._id}>
                <td>{day.Day}</td>
                <td colSpan={P1isLab ? 2 : 1}>{day.P1}</td>
                <td colSpan={P2isLab ? 2 : 1} hidden={P1isLab}>{day.P2}</td>
                <td colSpan={P3isLab ? 2 : 1} hidden={P2isLab}>{day.P3}</td>
                <td hidden={P3isLab}>{day.P4}</td>
                {(day.Day === 'จันทร์') ? <td rowSpan='5'>พัก</td> : <></>}
                <td colSpan={P6isLab ? 2 : 1}>{day.P6}</td>
                <td colSpan={P7isLab ? 2 : 1} hidden={P6isLab}>{day.P7}</td>
                <td colSpan={P8isLab ? 2 : 1} hidden={P7isLab}>{day.P8}</td>
                <td hidden={P8isLab}>{day.P9}</td>
            </tr>
        );
    });
    
    const dutyschedule = Duty.map(duty =>
    {
        return (
            <>
                <tr>
                    <td>{duty.day}</td>
                    <td>{duty.head}</td>
                    <td>{duty.erasebin}</td>
                    <td>{duty.table}</td>
                    <td>{duty.sweep}</td>
                    <td>{duty.mop}</td>
                </tr>
            </>
        );
    })

    const satschedule = SatSchedule.map(subj =>
    {
        return (
            <>
                <tr>
                    <td>{subj.date1}</td>
                    <td rowSpan={2}>{subj.subject}</td>
                    <td rowSpan={2}>{subj.teacher}</td>
                </tr>
                <tr>
                    <td>{subj.date2}</td>
                </tr>
            </>
       );
    });

    const testsch = TestSch.map(subj => 
    {
        if (subj.date === undefined)
        {
            return (
                <tr className='table-success'>
                    <td>{subj.time}</td>
                    <td>{subj.min}</td>
                    <td>{subj.sub}</td>
                    <td>{subj.pts}</td>
                </tr>
            );
        }
        else
        {
            return (
                <tr>
                    <th colSpan={5} className='table-primary'>{subj.date}</th>
                </tr>
            );
        }

    });

    return (
        <>
            <Container className='text-center contain'>
                <h1>ยินดีต้อนรับสู่ 515 AI Website</h1><br/><br/>
                <h1>ตารางเรียนชั้นมัธยมศึกษาปีที่ 5/15 (1/2565)</h1>
                <Table responsive bordered>
                    <thead className='align-middle'>
                        <tr className='table-secondary'>
                            <th>คาบเรียน</th>
                            <th>คาบ 1</th>
                            <th>คาบ 2</th>
                            <th>คาบ 3</th>
                            <th>คาบ 4</th>
                            <th>คาบ 5</th>
                            <th>คาบ 6</th>
                            <th>คาบ 7</th>
                            <th>คาบ 8</th>
                            <th>คาบ 9</th>
                        </tr>
                        <tr className='table-secondary'>
                            <th>เวลา</th>
                            <th>08:20 - 09:15</th>
                            <th>09:15 - 10:10</th>
                            <th>10:10 - 11:05</th>
                            <th>11:05 - 12:00</th>
                            <th>12:00 - 12:55</th>
                            <th>12:55 - 13:50</th>
                            <th>13:50 - 14:45</th>
                            <th>14:45 - 15:40</th>
                            <th>15:40 - 16:35</th>
                        </tr>
                        {scheduleMap}
                    </thead>
                </Table>
                {loading ? <Spinner animation="border" variant="primary" /> : <></>}
                <br/><br/>
                <h1>ตารางเวรประจำวัน (1/2565)</h1>
                <Table responsive bordered>
                    <thead className='align-middle'>
                        <tr className='table-secondary'>
                            <th>วัน</th>
                            <th>หัวหน้าเวร</th>
                            <th>ลบกระดาน, ทิ้งขยะ</th>
                            <th>ดูแลโต๊ะ</th>
                            <th>กวาดพื้น</th>
                            <th>ถูพื้น</th>
                        </tr>
                        {dutyschedule}
                    </thead>
                </Table><br/><br/>
                <h1 hidden>ตารางเรียนเสริมวันเสาร์ (1/2565)</h1>
                <Table responsive bordered hidden>
                    <thead className='align-middle'>
                        <tr className='table-secondary'>
                            <th>วันที่</th>
                            <th>วิชา</th>
                            <th>ครูผู้สอน</th>
                        </tr>
                        {satschedule}
                    </thead>
                </Table>
                <h1>ตารางสอบกลางภาคเรียนที่ 1/2565</h1>
                <Table responsive bordered>
                    <thead className='align-middle'>
                        <tr className='table-secondary'>
                            <th>เวลา</th>
                            <th>นาที</th>
                            <th>รายวิชา</th>
                            <th>คะแนน</th>
                        </tr>
                        {testsch}
                    </thead>
                </Table>
            </Container>
        </>
    );
}

export default Home;