import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import aesjs from 'aes-js';
import { Container, Form, Button, Row, Col } from 'react-bootstrap';

const Login = () => 
{
    const [user, setUser] = useState('');
    const [password, setPassword] = useState('');

	const navigate = useNavigate();

    const handleSubmit = (event) =>
    {
        event.preventDefault();
        //setLoading(true);

        const key = process.env.REACT_APP_AES_KEY.split(',').map((item) => {return parseInt(item, 10)})
        const passwordBytes = aesjs.utils.utf8.toBytes(password);
        const aesCtrl = new aesjs.ModeOfOperation.ctr(key, new aesjs.Counter(5));
        const encryptedBytes = aesCtrl.encrypt(passwordBytes);
        const encryptedHex = aesjs.utils.hex.fromBytes(encryptedBytes);

        axios.post(`${process.env.REACT_APP_API_LINK}/user/login`, {user, password: encryptedHex})
        .then((response) => 
        {
            localStorage.setItem('user', user);
            localStorage.setItem('accessToken', response.data.token);
            alert(`Welcome ${user}`);
            navigate('/');
            window.location.reload(false);
        })
    }

    return (
        <>
            <Container>
                <Row className='mt-5'>
                    <Col lg={5} md={6} sm={12} className='p-5 m-auto shadow-sm rounded header'>
                        <Form onSubmit={handleSubmit}>
                            <Form.Group controlId='formBasicText'>
                                <Form.Label className='text-light'>Username</Form.Label>
                                <Form.Control type='text' placeholder='Enter Username' onChange={(e) => setUser(e.target.value)}/>
                            </Form.Group><br/>
                            <Form.Group controlId='formBasicPassword'>
                                <Form.Label className='text-light'>Password</Form.Label>
                                <Form.Control type='password' placeholder='Password' onChange={(e) => setPassword(e.target.value)}/>
                            </Form.Group><br/>
                            <Button variant='primary btn-block' type='submit'>Login</Button>
                        </Form>
                    </Col>
                </Row>
            </Container>
        </>
    )
}

export default Login;